
import { mapGetters } from 'vuex'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
XButton
},
computed: {
...mapGetters([
'sidebar',
'language'
])
},
data() {
return {
showOne: false,
showTwo: false
}
},
methods: {
jump2Next() {
setTimeout(() => {
this.$router.push({path: '/swiper-img'})
}, 4000)
},
addText(timer=1000) {
setTimeout(() => {
if (!this.showOne) {
this.showOne = true
this.addText(3000)
} else if (!this.showTwo) {
this.showTwo = true
this.jump2Next()
}
}, timer)
},
},
mounted() {
this.$nextTick(function () {
this.addText()
})
}
}



